<template>
  <div class="result-container" v-loading.fullscreen.lock="loading">
    <div>方案编号：{{result.proCode}}</div>
    <div>
    <div class="title">
      油改水定制涂装体验方案
    </div>
    <h5>Part1: 油改水基本需求信息</h5>
    <div class="info-container client">
      <h5>客户名称: {{result.customer.company || ''}}</h5>
      <h5>联系人: {{result.customer.name}}</h5>
      <h5>联系电话: {{result.customer.phone}}</h5>
    </div>
    <div class="info-container product">
      <h5>家具风格: {{productStyle}}</h5>
      <h5>家具类型: {{productType}}</h5>
      <h5>家具底材: {{result.product.base}}</h5>
      <h5>涂装效果: {{productEffect}}</h5>
      <h5>涂料种类: {{paintingType}}</h5>
      <h5>面漆光泽: {{productLuster}}</h5>
      <h5>涂装工件类型: {{productWp}}</h5>
      <h5>水性漆希望采取的涂装方式: {{productProgram}}</h5>
    </div>
    <div id="interupt"><br></div>
    <div class="info-container">
      <h5>Part2：水性漆涂装建议方案</h5>
      <h5>2.1水性漆产品推荐</h5>
      <data-table :tableData="paintingTable.data" :columns="paintingTable.columns" :canMerge="true" class="data-table"></data-table>
      <div class="hint">说明：工艺推荐仅供参考，可结合实际涂装效果进行调整！</div>
    </div>
    <div class="info-container">
      <h5>2.2设备涂装及干燥方式建议</h5>
      <data-table :tableData="programTable.data" :columns="programTable.columns" :canMerge="false" class="data-table"></data-table>
    </div>
    </div>
    <div class="controller" id="no-print">
      <el-button type="primary" @click="print" icon="el-icon-printer">打印结果</el-button>
      <el-button @click="goToIndex">返回首页</el-button>
    </div>
  </div>
</template>

<script>
import {
  PAINTING_RESULT,
  PAINTING_COLUMN,
  DRY_PROGRAM,
  DEVICE_PROGRAM,
  DEVICE_COLUMNS,
  DRY_COLUMNS,
  MAPS
} from '../constants/infomation'
import DataTable from '@components/DataTable'
import { join } from 'lodash'
import axios from 'axios'

export default {
  name: 'result',
  data () {
    return {
      loading: true,
      result: {
        customer: {},
        product: {}
      },
      paintingTable: {
        data: [],
        columns: Object.freeze(PAINTING_COLUMN)
      },
      programTable: {
        data: [],
        columns: []
      },
      paintingRes: PAINTING_RESULT
    }
  },
  components: {
    DataTable
  },
  methods: {
    print () {
      window.print()
    },
    goToIndex () {
      this.$router.push({ name: 'userinfo' })
    }
  },
  computed: {
    productStyle () {
      let styles = [1, 2, 3, 4, 5, 6, 7, 8]
      return styles.includes(parseInt(this.result.product.style))
        ? MAPS.style[parseInt(this.result.product.style) - 1]
        : this.result.product.style
    },
    productType () {
      let types = [1, 2, 3, 4, 5, 6, 7, 8, 9]
      return types.includes(parseInt(this.result.product.type))
        ? MAPS.type[parseInt(this.result.product.type) - 1]
        : this.result.product.type
    },
    productEffect () {
      let effect = parseInt(this.result.product.effect)
      if ([1, 2, 3].includes(effect)) {
        return '透明开放'
      } else if ([4, 5, 6].includes(effect)) {
        return '实色开放'
      } else if ([7, 8, 9, 10, 11].includes(effect)) {
        return '透明封闭'
      } else if ([12, 13, 14, 15, 16].includes(effect)) {
        return '实色封闭'
      } else if ([17, 18].includes(effect)) {
        return '透明仿古'
      } else {
        return '实色仿古'
      }
    },
    paintingType () {
      return MAPS.paintingType[parseInt(this.result.product.effect) - 1]
    },
    productLuster () {
      let lusters = []
      let luster = parseInt(this.result.product.luster, 2)
      let count = 1
      let currentLus = 256
      while (count < 10) {
        if (luster & currentLus) {
          if (count === 9) {
            lusters.push('亮光')
          } else {
            lusters.push(count + '分光')
          }
        }
        count++
        currentLus /= 2
      }
      return join(lusters, ', ')
    },
    productWp () {
      return MAPS.wp[parseInt(this.result.product.workpiece) - 1]
    },
    productProgram () {
      return this.result.product.program === 0 ? '手工喷涂' : '设备自动化涂装'
    }
  },
  created () {
    if (!this.$route.query.pid) {
      return this.$router.push({ name: 'userinfo' })
    }
    axios({
      method: 'post',
      url: '/client',
      // url: 'http://192.168.0.249:5000/client',
      data: { pid: this.$route.query.pid },
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(res => {
      this.result = res.data
      switch (this.result.product.effect) {
        case 1:
          this.paintingTable.data = this.paintingRes['R1']
          break
        case 2:
        case 3:
          this.paintingTable.data = this.paintingRes['R2']
          break
        case 4:
          this.paintingTable.data = this.paintingRes['R3']
          break
        case 5:
        case 6:
          this.paintingTable.data = this.paintingRes['R4']
          break
        case 7:
        case 8:
          this.paintingTable.data = this.paintingRes['R5']
          break
        case 9:
          this.paintingTable.data = this.paintingRes['R6']
          break
        case 10:
        case 11:
          this.paintingTable.data = this.paintingRes['R7']
          break
        case 12:
        case 13:
          this.paintingTable.data = this.paintingRes['R8']
          break
        case 14:
          this.paintingTable.data = this.paintingRes['R9']
          break
        case 15:
        case 16:
          this.paintingTable.data = this.paintingRes['R10']
          break
        case 17:
        case 18:
          this.paintingTable.data = this.paintingRes['R11']
          break
        case 19:
        case 20:
          this.paintingTable.data = this.paintingRes['R12']
      }
      this.paintingTable.data.map(p => {
        p.base = this.result.product.base
      })
      if (this.result.product.program === 0) {
        this.programTable.data = DRY_PROGRAM
        this.programTable.columns = Object.freeze(DRY_COLUMNS)
      } else {
        let workpiece = 'S' + this.result.product.workpiece
        this.programTable.data = DEVICE_PROGRAM[workpiece]
        this.programTable.columns = Object.freeze(DEVICE_COLUMNS)
      }
      this.loading = false
    })
  }
}
</script>
<style lang="less">
@media print {
  #no-print {
    display: none;
  }
}
#interupt {
    page-break-before: always;
  }
@page {
  size: A4;
  margin: 38.07mm 30.00mm 50.92mm 30mm;
}
.data-table {
  width: 10rem;
}
.flex-column {
  display: flex;
  flex-direction: column;
}
.flex-row {
  display: flex;
  flex-direction: row;
}
h5 {
  margin: 10px 0;
  font-size: 16px;
}
.result-container {
  .flex-column;
  padding: 50px;
  text-align: left;
  // background: url('../../static/image/bg2.jpeg') no-repeat fixed;
  // background-size: 100% 100%;
}
.info-container {
  p {
    line-height: 30px;
  }
}
.title {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  span {
    float: right;
  }
}
.hint {
  margin: 10px 0;
  text-align: center;
  font-size: 14px;
  color: gray;
}
img {
  margin: 0 auto;
  display: block;
}
.controller {
  margin-top: 30px;
  text-align: center;
}

</style>



