<template>
  <div>
    <div class="info-container client">
      <h5>客户名称: {{information.customer.company || ''}}</h5>
      <h5>联系人: {{information.customer.name}}</h5>
      <h5>联系电话: {{information.customer.mobile}}</h5>
    </div>
    <div class="info-container product">
      <h5>家具风格: {{productStyle}}</h5>
      <h5>家具类型: {{productType}}</h5>
      <h5>家具底材: {{information.product.base}}</h5>
      <h5>涂装效果: {{productEffect}}</h5>
      <h5>涂料种类: {{paintingType}}</h5>
      <h5>面漆光泽: {{productLuster}}</h5>
      <h5>涂装工件类型: {{productWp}}</h5>
      <h5>水性漆希望采取的涂装方式: {{productProgram}}</h5>
    </div>
  </div>
</template>

<script>
import {
  MAPS
} from '../constants/infomation'
import { join } from 'lodash'
export default {
  name: 'info-box',
  data () {
    return {
    }
  },
  computed: {
    productStyle () {
      let styles = [1, 2, 3, 4, 5, 6, 7, 8]
      console.log(parseInt(this.information.product.style))
      return styles.includes(parseInt(this.information.product.style))
        ? MAPS.style[parseInt(this.information.product.style) - 1]
        : this.information.product.style
    },
    productType () {
      let types = [1, 2, 3, 4, 5, 6, 7, 8, 9]
      return types.includes(parseInt(this.information.product.type))
        ? MAPS.type[parseInt(this.information.product.type) - 1]
        : this.information.product.type
    },
    productEffect () {
      let effect = parseInt(this.information.product.effect)
      if ([1, 2, 3].includes(effect)) {
        return '透明开放'
      } else if ([4, 5, 6].includes(effect)) {
        return '实色开放'
      } else if ([7, 8, 9, 10, 11].includes(effect)) {
        return '透明封闭'
      } else if ([12, 13, 14, 15, 16].includes(effect)) {
        return '实色封闭'
      } else if ([17, 18].includes(effect)) {
        return '透明仿古'
      } else {
        return '实色仿古'
      }
    },
    paintingType () {
      return MAPS.paintingType[parseInt(this.information.product.effect) - 1]
    },
    productLuster () {
      let lusters = []
      let luster = parseInt(this.information.product.luster, 2)
      let count = 1
      let currentLus = 256
      while (count < 10) {
        if (luster & currentLus) {
          if (count === 9) {
            lusters.push('亮光')
          } else {
            lusters.push(count + '分光')
          }
        }
        count++
        currentLus /= 2
      }
      return join(lusters, ', ')
    },
    productWp () {
      return MAPS.wp[parseInt(this.information.product.workpiece) - 1]
    },
    productProgram () {
      return this.information.product.program === 0 ? '手工喷涂' : '设备自动化涂装'
    }
  },
  props: ['information']
}
</script>



