<template>
  <transition name="fade" mode="in-out">
    <div class="questions-container blue-bg">
      <div class="question-container">
        <div class="question-title">
          {{currentQuestion.question}}
        </div>
        <div class="question-content">
          <div v-if="currentQuestion.type === 'radio'">
            <div class="container"  v-if="currentQuestion.has_sub">
              <div v-for="(ans, index) in currentQuestion.answers" :key="index" class="sub-container">
                <h5>{{ans.subtitle}}</h5>
                <div class="radios-container">
                  <div v-for="subAns in ans.sub_answers" :key="subAns.value" class="radio-container">
                    <el-radio border v-model="currentAnswer" :label="subAns.value" class="custom-radio">
                      {{subAns.label}}
                    </el-radio>
                    <el-input v-model="inputValue" v-if="showInput(subAns.value)"></el-input>
                  </div>
                  <div class="radio-container blank-div" v-for="(i,index) in fourTime(ans.sub_answers)" :key="index + 1000"></div>
                </div>
              </div>
            </div>
            <div class="pointed-container container" v-if="currentQuestion.pointed">
              <el-radio v-model="currentAnswer" :key="index" border v-for="index in pointerTarget" :label="currentQuestion.answers[index].value" class="custom-radio">
                {{currentQuestion.answers[index].label}}
              </el-radio>
              <div class="custom-radio blank-div" v-for="(i,index) in triple" :key="index + 1000"></div>
            </div>
            <div class="normal-container container" v-if="!currentQuestion.has_sub && !currentQuestion.pointed">
              <el-radio  v-model="currentAnswer" border v-for="ans in currentQuestion.answers" :key="ans.value"  :label="ans.value" class="custom-radio">
                {{ans.label}}
              </el-radio>
              <el-radio border v-model="currentAnswer" label="other" class="custom-radio" v-if="currentQuestion.can_input">其他</el-radio>
              <div class="custom-radio blank-div" v-for="(i,index) in triple" :key="index + 1000"></div>
              <el-input v-model="inputValue" v-if="currentAnswer === 'other'"></el-input>
            </div>
          </div>
          <div v-if="currentQuestion.type === 'checkbox'">
            <div class="normal-container container" v-if="!currentQuestion.pointed">
              <el-checkbox-group v-model="multiAns">
                <el-checkbox border v-for="ans in currentQuestion.answers" :key="ans.value"  :label="ans.value" class="custom-checkbox">
                  {{ans.label}}
                </el-checkbox>
                <div class="custom-checkbox blank-div" v-for="(i,index) in triple" :key="index + 1000"></div>
              </el-checkbox-group>
            </div>
            <div class="pointed-container container" v-else>
              <el-checkbox-group v-model="multiAns">
                <el-checkbox border class="custom-checkbox" v-for="index in effectQuestion.pointer" :key="index"  :label="currentQuestion.answers[index].value">
                  {{currentQuestion.answers[index].label}}
                </el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
        </div>
        <div class="controller">
          <el-button type="primary" icon="el-icon-arrow-left" v-if="currentIndex !== 0" @click="goToLast">上一题</el-button>
          <el-button v-else @click="goToUser">返回用户信息</el-button>
          <el-button type="primary" v-if="currentIndex !== questions.length - 1" @click="goToNext" :disabled="!canGoNext">下一题<i class="el-icon-arrow-right el-icon--right"></i></el-button>
          <el-button type="primary" v-else @click="showDialog" :disabled="currentAnswer === ''">提交问卷</el-button>
        </div>
        <el-dialog
          title="确认信息"
          :visible.sync="dialogVisible"
          width="70%"
          class="dialog"
          >
          <Dialog :information='client'></dialog>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="sendAnswers">确 定</el-button>
          </span>
        </el-dialog>
      </div>
    </div>
  </transition>
</template>

<script>
import {
  MAPS
} from '../constants/infomation'
import QUESTIONS from '../constants/questions'
import axios from 'axios'

import Dialog from './Dialog'
export default {
  name: 'questionair',
  data () {
    return {
      questions: QUESTIONS.questions,
      currentIndex: 0,
      multiAns: [],
      currentAnswer: '',
      inputValue: '',
      effectQuestion: {},
      typeQuestion: {},
      answers: {
        product: {},
        painting: {},
        program: {}
      },
      styleQuestion: {},
      dialogVisible: false,
      client: {}
    }
  },
  components: {Dialog},
  created () {
    if (!this.$route.params.clientInfo) {
      this.$router.push({name: 'userinfo'})
    }
  },
  methods: {
    showDialog () {
      this.dialogVisible = true
      this.answers.painting.program = this.currentAnswer
      if (parseInt(this.answers.product.base)) {
        let baseType = parseInt(this.answers.product.base / 10)
        let baseName = this.answers.product.base % 10
        this.client = {
          customer: this.$route.params.clientInfo,
          product: {
            style: this.answers.product.style,
            type: this.answers.product.type,
            base: 'C' + baseType + MAPS.base[baseType - 1][baseName - 1],
            effect: this.answers.painting.effect,
            program: this.answers.painting.program,
            workpiece: this.answers.painting.workpiece,
            luster: this.answers.painting.luster
          }
        }
      }
    },
    goToUser () {
      this.$router.push({name: 'userinfo', params: {userInfo: this.$route.params.clientInfo}})
    },
    fourTime (ans) {
      let len = ans.length
      return 4 - len % 4
    },
    sendAnswers () {
      this.goToNext()
      if (parseInt(this.answers.product.base)) {
        let baseType = parseInt(this.answers.product.base / 10)
        let baseName = this.answers.product.base % 10
        this.answers.product.base = 'C' + baseType + MAPS.base[baseType - 1][baseName - 1]
      } else {
        this.answers.product.base = this.answers.product.baseType + this.answers.product.base
      }
      axios({
        method: 'post',
        // url: 'http://39.108.141.75:5000/addClient',
        url: '/addClient',
        // url: 'http://0.0.0.0:5000/addClient',
        data: {...this.answers, ...this.$route.params},
        headers: {
          'Content-Type': 'application/json'
        }
      }).then(res => {
        this.$router.push({name: 'result', query: {pid: res.data.pid}})
      })
    },
    initAns () {
      this.currentAnswer = ''
      this.inputValue = ''
      this.multiAns = []
    },
    popToAnswsers () {
      if (this.currentQuestion.value) {
        let questionInfo = this.currentQuestion.value.split('_')
        if (!this.answers[questionInfo[0]]) {
          this.answers[questionInfo[0]] = {}
        }
        if (this.currentQuestion.type === 'radio') {
          this.answers[questionInfo[0]][questionInfo[1]] = this.inputValue || this.currentAnswer
        } else {
          this.answers[questionInfo[0]][questionInfo[1]] = this.multiAns.reduce((prev, curr) => prev | curr).toString(2)
        }
      }
    },
    goToNext () {
      if (this.currentQuestion.value === 'painting_type') {
        this.typeQuestion = this.currentQuestion.answers[this.currentAnswer - 1]
      } else if (this.currentQuestion.value === 'painting_effect') {
        this.effectQuestion = this.currentQuestion.answers[this.currentAnswer - 1]
      } else if (this.currentQuestion.value === 'product_style') {
        this.styleQuestion = this.currentQuestion.answers[this.currentAnswer - 1]
      }
      this.popToAnswsers()
      if (this.currentQuestion.value === 'product_base') {
        if (parseInt(this.currentAnswer)) {
          this.answers.product.baseType = 'C' + parseInt(this.answers.product.base / 10)
        } else {
          this.answers.product.baseType = this.currentAnswer
        }
      }
      this.initAns()
      if (this.currentIndex !== this.questions.length - 1) {
        this.currentIndex++
      }
    },
    goToLast () {
      this.initAns()
      this.currentIndex--
    },
    showInput (value) {
      return this.currentAnswer === value && ['C1', 'C2', 'C3', 'C4'].includes(this.currentAnswer)
    }
  },
  computed: {
    currentQuestion () {
      return this.questions[this.currentIndex] || ''
    },
    canGoNext () {
      return parseInt(this.currentAnswer) || this.inputValue || this.multiAns.length
    },
    triple () {
      let len = this.currentQuestion.answers.length
      return this.currentQuestion.can_input ? !((len + 1) % 3) || 3 - ((len + 1) % 3) : !(len % 3) || 3 - len % 3
    },
    pointerTarget () {
      let pointerType = this.currentQuestion.pointed
      return this[`${pointerType}Question`].pointer
    }
  },
  watch: {
    currentIndex (val) {
      if (this.questions[val].value) {
        let infos = this.questions[val].value.split('_')
        if (!this.answers[infos[0]]) {
          this.answers[infos[0]] = {}
        }
        if (infos[1] === 'luster') {
          let luster = parseInt(this.answers.painting.luster, 2)
          let count = 1
          let currentLus = 256
          while (count < 10) {
            if (luster & currentLus) {
              this.multiAns.push(currentLus)
            }
            count++
            currentLus /= 2
          }
        } else {
          let savedAns = this.answers[infos[0]][infos[1]]
          if (savedAns) {
            if (!parseInt(savedAns)) {
              if (this.questions[val].value === 'product_base') {
                this.currentAnswer = this.answers.product.baseType
              } else {
                this.currentAnswer = 'other'
              }
              if (savedAns !== 'other') {
                this.inputValue = savedAns
              }
            } else {
              this.currentAnswer = savedAns
            }
          }
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.dialog {
  text-align: left;
}
.blue-bg {
  height: 100%;
  width: 100%;
  background: url('../../static/image/bg1.jpeg') no-repeat ;
  color: white;
  background-size: 100% 100%;
  .el-radio {
    color: white;
  }
  .el-checkbox {
    color: white;
  }
}
.questions-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.question-content {
    max-height: 9rem;
    overflow: auto;
}
.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding: .2rem .5rem;
}
.radios-container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  .custom-radio {
    flex: 0 1 4rem;
  }
}
.radio-container {
  position: relative;
  display: flex;
  flex-direction: row;
  margin: 0.1rem;
  flex: 0 0 4rem;
  .el-input {
    position: absolute;
    left: 4.2rem;
    top: .1rem;
    width: 3.8rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 100;
  }
}
.sub-container {
  width: 100%;
  h5 {
    font-size: 22px;
    margin: 20px 0;
  }
}
.question-title {
  font-weight: bolder;
  font-size: 36px;
}
.custom-radio {
  position: relative;
  flex: 1 1 4rem;
  margin: .1rem;
  text-align: left;
  padding: 0 .4rem;
}
.custom-checkbox {
  width: 3rem;
  margin: 10px;
  height: 50px;
  line-height: 50px;
  padding: 0;
}
.controller {
  margin-top: 10px;
}
.blank-div {
  padding: 0;
}
</style>



